@font-face {
  font-family: luxury;
  src: url(../../static/media/LuxuriousRoman-Regular.a36758be.ttf);
}
body {
  margin: 0; 
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  font-family: luxury;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace; 
}
html{
  height:100vh
}
#root{   
  height:100% 
}
   
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header { 
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes year-up-down {
  from{
    -webkit-transform: translateY(0em);
            transform: translateY(0em);
  }
  
   to{
    -webkit-transform: translateY(-0.3em);
            transform: translateY(-0.3em);
  }
}

@keyframes year-up-down {
  from{
    -webkit-transform: translateY(0em);
            transform: translateY(0em);
  }
  
   to{
    -webkit-transform: translateY(-0.3em);
            transform: translateY(-0.3em);
  }
}

#year{
  -webkit-animation: year-up-down 1s alternate infinite linear;
          animation: year-up-down 1s alternate infinite linear;
}

